// 
// t , b , c , d 
export function easeInOutCubic(t, b, c, d) {
  // 
  const cc = c - b
  // [0, 2]
  t /= d / 2
  // 
  if (t < 1) {
    return (cc / 2) * t * t * t + b
  }
  // 
  // t -= 2  t [-1, 0]
  //  (t -= 2) * t * t  y = x^3 [-1, 0]
  return (cc / 2) * ((t -= 2) * t * t + 2) + b
}

// 
export function getCurrentScrollTop() {
  return (
    window.pageYOffset ||
    document.body.scrollTop ||
    document.documentElement.scrollTop
  )
}
