import React, { Component } from "react"
import { StaticQuery, graphql } from "gatsby"

import { easeInOutCubic, getCurrentScrollTop } from "../../../utils/anim"

class Catalogue extends Component {
  render() {
    return (
      <StaticQuery
        query={graphql`
          {
            site {
              siteMetadata {
                post {
                  cat {
                    enable
                  }
                }
              }
            }
          }
        `}
        render={data => {
          const catConf = data.site.siteMetadata.post.cat
          return (
            <div className={catConf.enable ? "Catalogue-class" : "Hide-class"}>
              <div className="item-title"></div>
              <ul>
                {this.state.links.map(item => {
                  return (
                    <li
                      className={
                        this.state.active === item.id ? "catalogue-active" : ""
                      }
                      key={item.id}
                      data-depth={item.depth}
                    >
                      <a
                        onClick={e => {
                          e.preventDefault()
                          this.scrollTo(item.id)
                        }}
                        href={`#${item.id}`}
                      >
                        {item.value}
                      </a>
                    </li>
                  )
                })}
              </ul>
            </div>
          )
        }}
      />
    )
  }

  // links
  UNSAFE_componentWillMount() {
    const data = this.props.catData
    const links = data.map(h => ({
      //  gatsby-remark-autolink-headers 
      // -
      id: h.value
        .replace(/[`~!@#$%^&*()+=<>?:"{}|,.;'\\[\]~@#%&*+={}|]/g, "")
        .replace(/ /g, "-")
        .toLowerCase(),
      ...h,
    }))
    this.setState({
      links,
    })
  }

  // 
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll)
  }

  // 
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll)
  }

  // 
  handleScroll = () => {
    this.setState({
      active: this.getCurrentAnchor(),
    })
  }

  // 
  getCurrentAnchor() {
    const scrollTop = getCurrentScrollTop() + 20
    let active = null
    this.state.links.forEach(link => {
      const target = document.getElementById(link.id)
      if (!target || target.offsetTop > scrollTop) return
      if (!active) {
        active = target
      } else if (target.offsetTop >= active.offsetTop) {
        active = target
      }
    })

    return active ? active.id : null
  }

  //  id 
  scrollTo(id) {
    // 
    const scrollTop = getCurrentScrollTop()
    const target = document.getElementById(id)
    if (!target) {
      return
    }
    // 
    const targetScrollTop = target.offsetTop
    // 
    const startTime = Date.now()
    const frameFunc = () => {
      const timestamp = Date.now()
      // 
      const time = timestamp - startTime
      // 
      const nextScrollTop = easeInOutCubic(
        time,
        scrollTop,
        targetScrollTop,
        450
      )
      //  Y 
      window.scrollTo(window.pageXOffset, nextScrollTop)
      if (time < 450) {
        // 
        requestAnimationFrame(frameFunc)
      }
    }

    // IE
    requestAnimationFrame(frameFunc)
  }
}
export default Catalogue
